.tourists {
  position: relative;
  border: 1px solid var(--color-rinse);
  border-radius: 0 8px 8px 0;
  height: 64px;
  background-color: var(--color-light);
  transition: 0.3s;
  user-select: none;

  &__title {
    position: absolute;
    left: 16px;
    top: 8px;
    font-size: 14px;
    line-height: 20px;
    color: var(--color-great);
  }

  &__pinput {
    position: relative;
    display: flex;
    align-items: flex-end;
    padding: 19px 10px 10px 16px;
    width: 192px;
    height: 100%;
    font-weight: 400;
    font-style: normal;
    font-size: 16px;
    line-height: 20px;
    color: var(--color-ebony);
    transition: 0.3s;
  }

  &:hover {
    border: 1px solid var(--color-great);
    border-radius: 8px;
    transition: 0.3s;
  }

  @include tablet {
    width: 100%;
  }

 @include mobile {
    border-radius: 0 0 8px 8px;
  }
}

.tourists-body {
  position: absolute;
  left: 0;
  top: 76px;
  z-index: 100;
  border-radius: 8px;
  padding: 24px 20px;
  width: 260px;
  box-shadow: 0 8px 22px rgb(0 0 0 / 7%);
  background-color: var(--color-light);
  -ms-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

.tourists-elem {
  @include flex-all-sb;

  margin-bottom: 24px;
  width: 100%;
  min-width: 90px;
}

.tourists-item-adults {
  display: flex;
}
