@charset "UTF-8";
:root {
  --font-family: "Ubuntu", sans-serif;
  --content-width: 1230px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --color-light: #fff;
  --color-violet: #edf1fe;
  --color-aliceblue: #f0f8ff;
  --color-rinse: #d4d9de;
  --color-great: #7e8389;
  --color-sand: #f5f5f5;
  --color-primary: #ffe733;
  --color-active: #f3db25;
  --color-atlantis: #5ac82c;
  --color-blueberry: #518bc4;
  --color-sapphire: #0069b5;
  --color-blue: #4e3e8e;
  --color-bgreen: #d8f1c2;
  --color-gray: #424551;
  --color-ebony: #2e2e32;
  --grid-gap: 30px;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable value-keyword-case */
@font-face {
  font-family: "Ubuntu";
  src: url("../fonts/../fonts/UbuntuRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Ubuntu";
  src: url("../fonts/../fonts/UbuntuBold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 316px;
  min-height: 100%;
  font-size: 16px;
  -webkit-tap-highlight-color: transparent;
}

img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

input {
  outline: none;
  border: none;
  padding: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  cursor: pointer;
}

input[type=range]::-webkit-slider-thumb {
  height: 17px;
  width: 17px;
  border-radius: 50%;
  background: var(--color-blueberry);
  pointer-events: auto;
  -webkit-appearance: none;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.05);
}

input[type=range]::-moz-range-thumb {
  height: 17px;
  width: 17px;
  border: none;
  border-radius: 50%;
  background: #17A2B8;
  pointer-events: auto;
  -moz-appearance: none;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.05);
}

.site-container {
  overflow: hidden;
}

.active {
  opacity: 1;
  visibility: visible;
}

.rotate {
  transform: rotate(180deg) translateY(50%);
}

._lock {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.opacity {
  filter: opacity(30%);
}

.minus {
  width: 37px;
  height: 33px;
  background: url(../img/minus.svg) no-repeat;
}
.minus:hover {
  -webkit-transform: scale(1.03);
  transform: scale(1.03);
}

.plus {
  width: 32px;
  height: 33px;
  background: url(../img/plus.svg) no-repeat;
}
.plus:hover {
  -webkit-transform: scale(1.03);
  transform: scale(1.03);
}

.block {
  display: block !important;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: var(--color-light);
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.section-offset {
  padding-top: 80px;
}
@media (max-width: 576px) {
  .section-offset {
    padding-top: 60px;
  }
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

/* полоса прокрутки (скроллбар) */
::-webkit-scrollbar {
  width: 4px;
  /* ширина для вертикального скролла */
  height: 8px;
  /* высота для горизонтального скролла */
  /* background-color: #143861; */
}

/* ползунок скроллбара */
::-webkit-scrollbar-thumb {
  background-color: #843465;
  box-shadow: inset 1px 1px 10px #f3faf7;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #253861;
}

.grid {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  gap: var(--grid-gap);
}
@media (max-width: 576px) {
  .grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

