.swiper-slide--wrapper {
  padding: 0 0 18px;
  height: auto;
}

.icon-star {
  margin-right: 5px;
  width: 16px;
  height: 16px;
  fill: var(--color-primary);
}

.scale {
  display: inline-block;
  overflow: hidden;
  border-radius: 3px 3px 0 0;
}

.scale img {
  display: block;
  transition: 1s;
}

.scale img:hover {
  transform: scale(1.2);
}

.touch-area {
  position: relative;
}

.touch-area::before {
  content: "";
  position: absolute;
  left: -6px;
  top: 4px;
  display: block;
  padding: 16px;
  box-sizing: content-box;
}

.to-top {
  position: fixed;
  right: calc((100% - 1230px) / 2);
  bottom: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--color-ebony);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}
.to-top__text {
  font-size: 14px;
  opacity: 0;
  visibility: hidden;
}
.to-top__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  width: 40px;
  height: 40px;
  background: var(--color-primary);
  opacity: 0.7;
  transition: background-color 0.3s ease-in-out;
}
.to-top__icon svg {
  width: 24px;
  height: 24px;
  fill: var(--color-light);
  object-fit: cover;
}
.to-top__icon:hover {
  background-color: var(--color-active);
}
.to-top:hover .to-top__text {
  opacity: 1;
  visibility: visible;
}
.to-top--active {
  opacity: 1;
  visibility: visible;
}
@media (max-width: 1440px) {
