  content: "";
  position: absolute;
  left: 0;
  top: 0;
  border: 1px solid var(--color-rinse);
  border-radius: 100%;
  width: 22px;
  height: 22px;
  background: var(--color-light);
}
.contact-type__radio input:checked + label:after {
  opacity: 1;
  transform: scale(1);
}
.contact-type__radio input:checked + label:after, .contact-type__radio input:not(:checked) + label:after {
  content: "";
  position: absolute;
  left: 7px;
  top: 7px;
  border-radius: 100%;
