  }
}
@media (max-width: 768px) {
  .nav__link {
    color: var(--color-ebony);
  }
}

@media (max-width: 768px) {
  .menu {
    position: fixed;
    right: -300px;
    top: 0;
    z-index: 1;
    display: block;
    overflow: auto;
    width: 300px;
    height: 100vh;
    background-color: #fff;
    transition: right 0.3s;
  }
  .menu._active {
    right: 0;
  }
}

@media (max-width: 768px) {
  .burger {
    position: absolute;
    right: 15px;
    top: 25px;
    z-index: 5;
    display: block;
    width: 30px;
    height: 18px;
    cursor: pointer;
  }
  .burger span {
    position: absolute;
    left: 0;
    top: calc(50% - 1px);
    width: 100%;
    height: 2px;
    background-color: #fff;
    transition: all 0.3s ease 0s;
  }
  .burger span:first-child {
    top: 0;
  }
  .burger span:last-child {
    top: auto;
    bottom: 0;
  }
  .burger._active span {
    transform: scale(0);
  }
  .burger._active span:first-child {
    top: calc(50% - 1px);
    transform: rotate(-45deg);
  }
  .burger._active span:last-child {
    bottom: calc(50% - 1px);
    transform: rotate(45deg);
  }
}

@media (max-width: 768px) {
  .overlay {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.7);
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
  }
  .overlay._active {
    opacity: 1;
    visibility: visible;
  }
}

