.switch {
  position: relative;
  display: inline-block;
  width: 43px;
  height: 22px;
}
.switch__slider {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
  cursor: pointer;
}
.switch__slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 2px;
  bottom: 2px;
  background-color: var(--color-light);
  transition: 0.4s;
}
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

input:checked + .switch__slider {
  background-color: var(--color-atlantis);
}

input:focus + .switch__slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .switch__slider:before {
  transform: translateX(21px);
}

/* Rounded sliders */
.round {
  border-radius: 34px;
}
.round:before {
  border-radius: 50%;
}